// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

$grey: #f4f4f4;
$facebook-blue: #3B5998;
$twitter-blue: #4099FF;
$github-black: #000000;
$google-red: #d62d20;

/* Custom Stylesheet */
/**
 * Use this file to override Materialize files so you can update
 * the core Materialize files in the future
 *
 * Made By MaterializeCSS.com
 */

nav ul a,
nav .brand-logo {
  color: #444;
}

.learn-nav {
    font-weight: 100;
    padding: 0 10px 10px 10px;
}

.center{
    margin: 0 auto;
    width: 30%;
}

.facebook-blue {
    color: $facebook-blue;
}

.twitter-blue {
    color: $twitter-blue;
}

.github-black {
    color: $github-black;
}

.google-red {
    color: $google-red;
}

p {
  line-height: 2rem;
}

.button-collapse {
  color: #26a69a;
}

.parallax-container {
  min-height: 380px;
  line-height: 0;
  height: auto;
  color: rgba(255,255,255,.9);
}
  .parallax-container .section {
    width: 100%;
  }

@media only screen and (max-width : 992px) {
  .parallax-container .section {
    position: absolute;
    top: 40%;
  }
  #index-banner .section {
    top: 10%;
  }
}

@media only screen and (max-width : 600px) {
  #index-banner .section {
    top: 0;
  }
}

.icon-block {
  padding: 0 15px;
}
.icon-block .material-icons {
  font-size: inherit;
}

footer.page-footer {
  margin: 0;
}